import React from 'react'

const ReferModal = () => {
  return (
    <>
       <div className="offcanvas offcanvas-end editProbationReview" tabindex="-1" id="editProbationReviews">
        <div className="offcanvas-header">
            <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Candidate Details</h5>
            <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
        </div>
        <div className="offcanvas-body">
            <div className="probationForm">
                <div className="formPanel referNowform">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="form-group mb-3">
                                <label>First Name</label>
                                <input type="text" className="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-3">
                                <label>Last Name</label>
                                <input type="text" className="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-3">
                                <label>Email *</label>
                                <input type="email" className="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-3">
                                <label>Mobile Number *</label>
                                <input type="number" className="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-3">
                                <label>Designation *</label>
                                <input type="text" className="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-3">
                                <label>DOB *</label>
                                <input type="text" className="form-control" placeholder="Enter here"/> 
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-3">
                                <label>Full Address *</label>
                                <input type="text" className="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-3">
                                <label>Pincode *</label>
                                <input type="text" className="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-3">
                                <label>Experience *</label>
                                <input type="text" className="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-3">
                                <label>Technology Knowledge *</label>
                                <input type="text" className="form-control" placeholder="Enter here" />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-3">
                                <label>Resume *</label>
                                <div className="position-relative">
                                    <input type="text" className="form-control" value="Choose File" />
                                    <div className="inputuploader"><img src="images/file-input.svg" alt="" width="26"/>
                                        <input type="file" name="upload-img" accept="image/*" /></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="d-flex align-items-center justify-content-end gap-2 border-top pt-3">
                <button type="button" className="btn btn-org-gradient" data-bs-toggle="modal"
                    data-bs-target="#otpModal">Submit</button>
                <button type="button" className="btn btn-secondary">Back</button>
            </div>
        </div>
    </div>
    </>
  )
}

export default ReferModal
